@import url(https://fonts.googleapis.com/css?family=Mr+Dafoe);
@import url(https://fonts.googleapis.com/css?family=Lobster);
@import url(https://fonts.googleapis.com/css?family=Monoton);
@import url(https://fonts.googleapis.com/css?family=Yellowtail);
@import url(https://fonts.googleapis.com/css?family=Monoton);
body{
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.App{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-image: url(/path-finding-visualizer/static/media/sky-background.b286d042.jpg);
}
.header{
    height: 300px;
    background-image: url(/path-finding-visualizer/static/media/header-background.81ecc5fa.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: grid;
    grid-template-columns: auto 30%;
    grid-template-rows: auto;
}

.title{
    margin: 0px 0px 0px 0px;
    color: rgb(234,0,215);
    font-family: 'Monoton', cursive;
    font-style: italic;
    font-weight: lighter;
    font-size: 38px;
}

select{
    background: rgb(16,10,36);
    color: rgb(234,0,215);
    border: transparent;
    /*font-family: 'Monoton', cursive;*/
    font-style: italic;
    font-weight: lighter;
    font-size: 30px;
    grid-column: 1;
}
select:focus{
    outline: 0;
}

.start-search{
    border: none;
    background-color: rgb(248,205,60);
    width: 75px;
    font-size: 16pt;
    border-radius: 5px;
    margin-left: 10px;
    height: 57px;
}

label{
    color: white;
}

.buttons-container{
    margin-top: auto;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 300px;
    grid-column: 2;
    grid-row-start: 1;
    grid-row-end: 5 ;
}

.animated-button{
    margin-left: auto;
    text-align: right;
    justify-self: right;
    float: right;
    width: 50%;
    height: 10%;
    font-size: 16px;
    max-width: 300px;
    background-color: transparent;
    border: none;
    color: white;
}

.animated-button:hover{
    -webkit-animation: fadeInAnimation .2s linear forwards;
            animation: fadeInAnimation .2s linear forwards;
    font-size: 20px;
}

button:focus{
    outline: 0;
}

hr{
    margin: 5px 0px 5px auto;
    height: 1px;
    width: 50%;
    max-width: 300px;
    background-color: rgb(234,0,215);
    border: none;
}

.label-element-container{
    display: flex;
    width: 30%;
    flex-direction: column;
    margin-bottom: 10px;
}

#speed-select{
    grid-column: 1;
}

@-webkit-keyframes fadeInAnimation {
    0% {
        width: 30%;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
    }
    100% {
        width: 100%;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
        background-color: rgb(234,0,215);
    }
  }

@keyframes fadeInAnimation {
    0% {
        width: 30%;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
    }
    100% {
        width: 100%;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
        background-color: rgb(234,0,215);
    }
  }
.grid-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.grid{
    display: grid;
    background-color: transparent;
    /*grid-template-columns: repeat(30,auto);*/
    justify-content: left;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.node{
    border: solid 1px rgb(234,0,215,.3);
    background-color: transparent;
    -webkit-user-select: none;
}

.openList{
    -webkit-animation-name: openAnimation;
            animation-name: openAnimation;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

.openListInstant{
  background-color: rgb(0,121,254);
}

.searched{
    -webkit-animation-name: searchedAnimation;
            animation-name: searchedAnimation;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

.searchedInstant{
  background-color:  rgb(0,255,249);
}

.start{
    background-color: rgb(6,255,0,.8) !important;
}

.finish{
    background-color: rgb(248,0,8,.8) !important;
}

.wall{
    -webkit-animation-name: wallAnimation;
            animation-name: wallAnimation;
    -webkit-animation-duration: .2s;
            animation-duration: .2s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

.shortestPath{
  -webkit-animation-name: shortestPathAnimation;
          animation-name: shortestPathAnimation;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
.shortestPathInstant{
  background-color: rgb(251,231,48);
}
  
@-webkit-keyframes shortestPathAnimation {
  0% {
    transform: scale(.2);
    background-color: rgb(0,255,249);
    border-radius: 100%;
  }

  75% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
    background-color: rgb(251,231,48);
  }
}
  
@keyframes shortestPathAnimation {
  0% {
    transform: scale(.2);
    background-color: rgb(0,255,249);
    border-radius: 100%;
  }

  75% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
    background-color: rgb(251,231,48);
  }
}

@-webkit-keyframes wallAnimation {
  0% {
    transform: scale(.2);
    background-color: black;
  }
  75% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
    background-color: rgb(234,0,215);
  }
}

@keyframes wallAnimation {
  0% {
    transform: scale(.2);
    background-color: black;
  }
  75% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
    background-color: rgb(234,0,215);
  }
}

@-webkit-keyframes openAnimation {
    0% {
      transform: scale(.2);
      background-color: black;
      border-radius: 100%;
    }
  
    75% {
      transform: scale(1.1);
      background-color: rgb(0,121,254);
    }
  
    100% {
      transform: scale(1);
      background-color: rgb(0,121,254);
    }
}

@keyframes openAnimation {
    0% {
      transform: scale(.2);
      background-color: black;
      border-radius: 100%;
    }
  
    75% {
      transform: scale(1.1);
      background-color: rgb(0,121,254);
    }
  
    100% {
      transform: scale(1);
      background-color: rgb(0,121,254);
    }
}

@-webkit-keyframes searchedAnimation {
    0% {
      transform: scale(.2);
      background-color: rgb(255,0,193);
      border-radius: 100%;
    }
  
    75% {
      transform: scale(1.1);
      background-color: rgb(0,255,249);
    }
  
    100% {
      transform: scale(1);
      background-color: rgb(0,255,249);
    }
}

@keyframes searchedAnimation {
    0% {
      transform: scale(.2);
      background-color: rgb(255,0,193);
      border-radius: 100%;
    }
  
    75% {
      transform: scale(1.1);
      background-color: rgb(0,255,249);
    }
  
    100% {
      transform: scale(1);
      background-color: rgb(0,255,249);
    }
}
